import React from "react";
import Masonry from "react-masonry-css";
import Image from "react-bootstrap/Image";
import Moment from "moment";
import { saveAs } from "file-saver";

import Url from "../api/url";

const breakpoints = {
  default: 5,
  1100: 4,
  900: 3,
  700: 2,
  500: 1,
};

function ImageList({ imageList, handleOpenImage }) {
  const saveFile = (img) => {
    saveAs(img, "example.png");
  };
  // console.log(imageList);

  return (
    <Masonry
      breakpointCols={breakpoints}
      className="my-masonry-grid"
      columnClassName="my-masonry-grid_column"
    >
      {imageList.map((img, index) => (
        <div key={index} className="containerimage">
          <p>{img.title}</p>
          <Image
            className="my-masonry-img"
            src={img.image}
            fluid
            width={"100%"}
            onClick={() => handleOpenImage(img.id)}
          />
          <div className="metadata">
            <div>{img.title}</div>
            <button onClick={() => saveFile(img.image)}>download</button>
          </div>
        </div>
      ))}
    </Masonry>
  );
}

export default ImageList;
