import { useState } from "react";
import axios from "axios";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Select from "react-select";
import DatePicker from "react-date-picker";
import Moment from "moment";

import Countries from "../api/countries";
import Url from "../api/url";

function Search({
  updateSearchList,
  get_images,
  // showForm,
  setSearchStr,
  setImageCount,
  countItems,
  setResetPaginator,
}) {
  const [title, setTitle] = useState("");
  const [orientation, setOrientation] = useState("");
  const [country, setCountry] = useState("");
  const [dateFrom, setDateFrom] = useState("");
  const [dateTo, setDateTo] = useState("");
  const [widthTo, setWidthTo] = useState("");
  const [widthFrom, setWidthFrom] = useState("");
  const [heightTo, setHeightTo] = useState("");
  const [heightFrom, setHeightFrom] = useState("");
  const [sizeFrom, setSizeFrom] = useState("");
  const [sizeTo, setSizeTo] = useState("");
  const [author, setAuthor] = useState("");

  const headers = {
    Authorization: `Token ${localStorage.getItem("token")}`,
  };

  const doSearch = () => {
    var cleanstring = title.replace(",", "");
    cleanstring = title.split(" ");
    var titleSearch = "";
    cleanstring.map((item) => {
      if (item.length > 0) {
        titleSearch = titleSearch + "," + item.trim();
      }
    });
    if (titleSearch.length > 0) {
      titleSearch = titleSearch.substring(1);
    }

    var orientationSearch = orientation;
    if (orientation == 0) {
      orientationSearch = "";
    }

    if (dateFrom) {
      var dateFromSearch = Moment(dateFrom).format("YYYY-MM-DD");
    } else {
      var dateFromSearch = "";
    }

    if (dateTo) {
      var dateToSearch = Moment(dateTo).format("YYYY-MM-DD");
    } else {
      var dateToSearch = "";
    }

    const searchString = `date__gte=${dateFromSearch}&date__lte=${dateToSearch}&orientation=${orientationSearch}&country=${country}&width__gte=${widthFrom}&width__lte=${widthTo}&height__gte=${heightFrom}&height__lte=${heightTo}&size__gte=${sizeFrom}&size__lte=${sizeTo}&author__icontains=${author}&search=${titleSearch}&ordering=-uploaded_at`;

    setSearchStr(searchString);

    axios
      .get(Url.baseUrl + "/products/?" + searchString, {
        headers: headers,
      })
      .then((res) => {
        updateSearchList(res.data.results);
        setImageCount(res.data.count);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const handleCountryChange = (e) => {
    setCountry(e.value);
  };

  const resetForm = () => {
    setTitle("");
    setOrientation("");
    setCountry("");
    setDateFrom("");
    setDateTo("");
    setWidthFrom("");
    setWidthTo("");
    setHeightFrom("");
    setHeightTo("");
    setSizeFrom("");
    setSizeTo("");
    setAuthor("");
    setSearchStr("");
    get_images(0); //fn in Products.js
    countItems();
    setResetPaginator(new Date().getTime());
  };

  return (
    <Form style={{ backgroundColor: "#a0c0a0", padding: 10 }}>
      <Row>
        <Col sm={4}>
          <Row>
            <Col sm={8}>
              <Form.Group className="mb-3" controlId="f1">
                <Form.Label>Title</Form.Label>
                <Form.Control
                  name="title"
                  type="text"
                  value={title}
                  required
                  placeholder="Space sparated words"
                  onChange={(e) => setTitle(e.target.value)}
                  size="sm"
                />
              </Form.Group>
            </Col>
            <Col sm={4}>
              <Form.Group className="mb-3" controlId="f1">
                <Form.Label>Author</Form.Label>
                <Form.Control
                  name="author"
                  type="text"
                  value={author}
                  required
                  placeholder="Name"
                  onChange={(e) => setAuthor(e.target.value)}
                  size="sm"
                />
              </Form.Group>
            </Col>
          </Row>
          <Row>
            <Col sm={4}>
              <Form.Group controlId="formBasicSelect">
                <Form.Label>Orientation</Form.Label>
                <Form.Select
                  value={orientation}
                  onChange={(e) => {
                    setOrientation(e.target.value);
                  }}
                >
                  <option value={0}></option>
                  <option value={1}>Horizontal</option>
                  <option value={2}>Vertical</option>
                  <option value={3}>Square</option>
                </Form.Select>
              </Form.Group>
            </Col>
            <Col>
              <Form.Label>Country</Form.Label>
              <Select
                options={Countries.countries}
                onChange={handleCountryChange}
                value={Countries.countries.filter(function (option) {
                  return option.value === country;
                })}
              />
            </Col>
          </Row>
        </Col>
        <Col sm={2}>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Label>Date from</Form.Label>
            <br />
            <DatePicker
              onChange={setDateFrom}
              value={dateFrom}
              format="dd-MM-y"
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Label>Date to</Form.Label>
            <br />
            <DatePicker onChange={setDateTo} value={dateTo} format="dd-MM-y" />
          </Form.Group>
        </Col>
        <Col sm={2}>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Label>Width From</Form.Label>
            <Form.Control
              name="widthfrom"
              type="number"
              value={widthFrom}
              placeholder="px"
              maxLength="10"
              onKeyPress={(event) => {
                if (!/[0-9]/.test(event.key)) {
                  event.preventDefault();
                }
              }}
              onChange={(e) => setWidthFrom(e.target.value)}
              size="sm"
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Label>Width To</Form.Label>
            <Form.Control
              name="widthto"
              type="number"
              value={widthTo}
              placeholder="px"
              maxLength="10"
              onKeyPress={(event) => {
                if (!/[0-9]/.test(event.key)) {
                  event.preventDefault();
                }
              }}
              onChange={(e) => setWidthTo(e.target.value)}
              size="sm"
            />
          </Form.Group>
        </Col>
        <Col sm={2}>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Label>Height From</Form.Label>
            <Form.Control
              name="heightfrom"
              type="number"
              value={heightFrom}
              placeholder="px"
              maxLength="10"
              onKeyPress={(event) => {
                if (!/[0-9]/.test(event.key)) {
                  event.preventDefault();
                }
              }}
              onChange={(e) => setHeightFrom(e.target.value)}
              size="sm"
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Label>Height To</Form.Label>
            <Form.Control
              name="heightto"
              type="number"
              value={heightTo}
              placeholder="px"
              maxLength="10"
              onKeyPress={(event) => {
                if (!/[0-9]/.test(event.key)) {
                  event.preventDefault();
                }
              }}
              onChange={(e) => setHeightTo(e.target.value)}
              size="sm"
            />
          </Form.Group>
        </Col>
        <Col sm={2}>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Label>Size From</Form.Label>
            <Form.Control
              name="sizefrom"
              type="number"
              value={sizeFrom}
              placeholder="bytes"
              maxLength="15"
              onKeyPress={(event) => {
                if (!/[0-9]/.test(event.key)) {
                  event.preventDefault();
                }
              }}
              onChange={(e) => setSizeFrom(e.target.value)}
              size="sm"
            />
          </Form.Group>
          <Form.Group className="mb-3" controlId="f1">
            <Form.Label>Size To</Form.Label>
            <Form.Control
              name="Sizetto"
              type="number"
              value={sizeTo}
              placeholder="bytes"
              maxLength="15"
              onKeyPress={(event) => {
                if (!/[0-9]/.test(event.key)) {
                  event.preventDefault();
                }
              }}
              onChange={(e) => setSizeTo(e.target.value)}
              size="sm"
            />
          </Form.Group>
        </Col>
      </Row>
      <Row>
        <Col md={2} style={{ textAlign: "left" }}>
          {/* <Button variant="success" onClick={showForm}>
            Add Image
          </Button> */}
        </Col>
        <Col md={{ span: 5, offset: 5 }} style={{ textAlign: "right" }}>
          <Button
            variant="secondary"
            onClick={resetForm}
            style={{ marginRight: 10 }}
          >
            Reset Form
          </Button>
          <Button variant="primary" onClick={doSearch}>
            Search
          </Button>
        </Col>
      </Row>
      <Row></Row>
    </Form>
  );
}

export default Search;
