import React, { useState, useEffect } from "react";
import axios from "axios";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Select from "react-select";
import ReactPaginate from "react-paginate";
import Modal from "react-bootstrap/Modal";

import Url from "../api/url";
import ImageList from "../components/ImageList";
import Topbar from "../components/Topbar";
import Login from "../pages/Login";
import Logout from "../pages/Logout";
import Search from "../components/Search";

function Images(props) {
  const [loginas, setLoginas] = useState("");
  const [loading, setLoading] = useState(true);
  const [imageList, setImageList] = useState([]);
  const [imageCount, setImageCount] = useState(1);
  const [searchStr, setSearchStr] = useState("");
  const [resetPaginator, setResetPaginator] = useState(new Date().getTime());
  const [showLogin, setShowLogin] = useState(true);
  const [showLogout, setShowLogout] = useState(false);

  const headers = {
    Authorization: `Token ${localStorage.getItem("token")}`,
  };

  useEffect(() => {
    get_authuser();
    get_images(0);
    countItems();
  }, []);

  const get_authuser = () => {
    axios
      .get(Url.baseUrl + "/api/authentication/user/", {
        headers: headers,
      })
      .then((res) => {
        console.log(res.data);
        setLoginas(res.data.username);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const get_images = (myoffset) => {
    axios
      .get(
        Url.baseUrl +
          `/products/?search=${searchStr}&limit=${itemsPerPage}&offset=${myoffset}&ordering=-uploaded_at`,
        {
          headers: headers,
        }
      )
      .then((res) => {
        setImageList(res.data.results);
        // console.log("count: " + res.data.count);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const countItems = () => {
    axios
      .get(Url.baseUrl + "/products/", {
        headers: headers,
      })
      .then((res) => {
        setImageCount(res.data.count);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  //Paginator
  const itemsPerPage = 10;
  const [currentItems, setCurrentItems] = useState(null);
  const [pageCount, setPageCount] = useState(0);
  const [offset, setOffset] = useState(0);
  const [itemOffset, setItemOffset] = useState(0);

  useEffect(() => {
    // Fetch items from another resources.
    const endOffset = itemOffset + itemsPerPage;
    // console.log(`Loading items from ${itemOffset} to ${endOffset}`);
    // setCurrentItems(items.slice(itemOffset, endOffset));
    setPageCount(Math.ceil(imageCount / itemsPerPage));
  }, [itemOffset, itemsPerPage, imageCount]);

  // Invoke when user click to request another page.
  const handlePageClick = (event) => {
    const newOffset = (event.selected * itemsPerPage) % imageCount;
    setOffset(newOffset);
    // console.log(
    //   `User requested page number ${event.selected}, which is offset ${newOffset}`
    // );
    setItemOffset(newOffset);
    get_images(newOffset);
  };

  const handleOpenImage = () => {
    console.log("open image");
  };

  const handleCloseModal = () => setShowLogout(false);

  const updateSearchList = (res) => {
    setImageList(res);
  };

  return (
    <Container>
      <Topbar
        setShowLogout={setShowLogout}
        showLogin={showLogin}
        loginas={loginas}
      />
      {showLogin && (
        <Login setShowLogin={setShowLogin} setLoginas={setLoginas} />
      )}
      {showLogout && (
        <Modal show={showLogout} onHide={handleCloseModal}>
          <Logout setShowLogin={setShowLogin} setShowLogout={setShowLogout} />
        </Modal>
      )}
      {!showLogin && (
        <>
          <Row>
            <Col>
              <Search
                updateSearchList={updateSearchList}
                get_images={get_images}
                // showForm={showForm}
                setSearchStr={setSearchStr}
                setImageCount={setImageCount}
                countItems={countItems}
                setResetPaginator={setResetPaginator}
              />
            </Col>
          </Row>
          <Row style={{ marginTop: 10 }}>
            <Col>
              <ReactPaginate
                nextLabel="next >"
                onPageChange={handlePageClick}
                pageRangeDisplayed={3}
                marginPagesDisplayed={2}
                pageCount={pageCount}
                previousLabel="< previous"
                pageClassName="page-item"
                pageLinkClassName="page-link"
                previousClassName="page-item"
                previousLinkClassName="page-link"
                nextClassName="page-item"
                nextLinkClassName="page-link"
                breakLabel="..."
                breakClassName="page-item"
                breakLinkClassName="page-link"
                containerClassName="pagination"
                activeClassName="active"
                renderOnZeroPageCount={null}
              />
            </Col>
            <Col md={{ span: 2, offset: 4 }} style={{ textAlign: "right" }}>
              <h4>Total: {imageCount}</h4>
            </Col>
          </Row>
          <Row>
            <ImageList
              imageList={imageList}
              handleOpenImage={handleOpenImage}
            />
          </Row>
        </>
      )}
    </Container>
  );
}

export default Images;
